import PropTypes from 'prop-types';
import React, { useState } from 'react';
import { Button, Carousel, Col, Image, Modal, Row } from 'react-bootstrap';
import extractNumber from '../logic/extractNumber';
import ImageCard from './ImageCard';
import './ImageList.css';

const ImageList = ({ images }) => {
  const [showMore, setShowMore] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [modalIndex, setModalIndex] = useState(0);

  const onButtonClick = () => setShowMore((prevShowMore) => !prevShowMore);
  const onImageClick = (i) => {
    setShowModal(true);
    setModalIndex(i);
  };
  const onCloseClick = () => setShowModal(false);

  const numberOfItems = showMore ? images.length : 10;
  return (
    <>
      <div className="image-list">
        {images.slice(0, numberOfItems).map((image, i) => {
          const imageNumber = extractNumber(image);

          return (
            <ImageCard
              key={imageNumber}
              src={image}
              alt={`photo-${imageNumber}`}
              onClick={() => onImageClick(i)}
            />
          );
        })}
      </div>
      <Row xs={1} className="mx-0 mt-3 g-0 justify-content-center">
        <Col xs="auto">
          <Button variant={showMore ? 'outline-danger' : 'outline-info'} onClick={onButtonClick}>
            {showMore ? 'Show less' : `Show ${images.length - numberOfItems} more`}
          </Button>
        </Col>
      </Row>
      <Modal show={showModal} onHide={onCloseClick} size="lg" centered>
        <Modal.Header closeButton>
          <Modal.Title>Photo {modalIndex + 1}</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Carousel activeIndex={modalIndex} onSelect={onImageClick} interval={null}>
            {images.map((image) => {
              const imageNumber = extractNumber(image);

              return (
                <Carousel.Item key={imageNumber}>
                  <Image src={image} alt={`photo-${imageNumber}`} fluid />
                </Carousel.Item>
              );
            })}
          </Carousel>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={onCloseClick}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};

ImageList.propTypes = {
  images: PropTypes.arrayOf(PropTypes.string).isRequired,
};

export default ImageList;
